* {
  box-sizing: border-box;
}

:root {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

img {
  width: 200px;
  height: 200px;

  border-radius: 200px;

  transition-duration: 1s;
}

.people {
  --maxwidth: 430px;
  --column-count: 2;

  max-width: var(--maxwidth);
  margin: 0 auto;
  padding-top: 30px;
  padding-bottom: 100px;

  display: grid;
  grid-template-columns: repeat(var(--column-count), 1fr);
  gap: 40px 30px;

  @media (width >= 500px) {
    --column-count: 2;
  }

  @media (width >= 1000px) {
    --column-count: 4;
    --maxwidth: 890px;
  }

  .person {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 12px;
    border-radius: 12px;

    .photo {
      position: relative;
      width: 200px;
      height: 200px;

      .rank {
        width: 32px;
        height: 32px;
        position: absolute;

        right: 10px;
        top: 50%;
        bottom: 50%;
        transform: translate(0, -50%);

        background-color: #fff;
        color: #8b0000;
        font-size: 16px;
        line-height: 32px;
        text-align: center;

        border-radius: 50%;
      }
    }

    .details {
      .name {
        margin-top: 8px;

        font-size: 20px;
        line-height: 23px;
        color: #2f4f4f;
      }

      .extra {
        display: none;
        margin-top: 9px;
        font-size: 18px;
        line-height: 18px;
        text-align: center;

        .sport {
          &::after {
            content: ",";
          }
        }

        .pay {
          &::before {
            content: "$";
          }

          &::after {
            content: "M";
          }
        }
      }
    }

    &:hover {
      scale: (1.2);
      background-color: #eeeeee;

      img {
        border-radius: 60px;
      }

      .extra {
        display: block;
      }
    }
  }
}
